const validasiHeadPindahBarang = (value) => {
  const errors = {};
  if (!value.tanggal) {
    errors.tanggal = "Tidak Boleh kosong";
  }
  if (!value.dari) {
    errors.dari = "Tidak Boleh kosong";
  }
  if (!value.ke) {
    errors.ke = "Tidak Boleh kosong";
  }
  if (!value.kategori) {
    errors.kategori = "Tidak Boleh kosong";
  }
  if (!value.noBon) {
    errors.noBon = "Tidak Boleh kosong";
  }
  return errors;
};

export default validasiHeadPindahBarang;
