import React, { Component } from "react";
import { connect } from "react-redux";
import ReactTooltip from "react-tooltip";
import { Field, reduxForm } from "redux-form";
import { showModal } from "../../actions/ModalAction";
import { getListPindahBarang } from "../../actions/pindahBarangAction";
import { getLokasiPindah } from "../../actions/supplierAction";
import { deleteIndex } from "../../deleteLocalStorage";
import { getToday, SkeletonLoading } from "../../getDate";
import GlobalTabel from "../../GlobalComponent/GlobalTabel";
import { renderField, renderFieldSelect } from "../../renderField";
import validasiHeadPindahBarang from "../../Validations/validasiHeadPindahBarang";
import FormBarcode from "./FormBarcode";

class HeadPindahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      supplier: false,
      listKategori: [
        {
          nama: "AMBIL BARANG",
        },
        {
          nama: "KEMBALI STOK",
        },
        {
          nama: "KOREKSI STOK",
        },
        {
          nama: "MUTASI BAHAN",
        },
        {
          nama: "PENGAMBILAN SALES",
        },
        {
          nama: "PINDAH",
        },
        {
          nama: "PINDAH GUDANG REPARASI",
        },
        {
          nama: "RETURN SALES",
        },
        {
          nama: "SALAH INPUT",
        },
        {
          nama: "SALAH PINDAH",
        },
        {
          nama: "STOK AWAL",
        },
        {
          nama: "TAMBAH BARANG",
        },
      ],
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Barang",
        },
        {
          dataField: "kadar_modal",
          text: "Kat Harga",
        },
        {
          dataField: "kadar_cetak",
          text: "Kadar",
        },
        {
          dataField: "pkg",
          text: "Pkg (Gr)",
        },
        {
          dataField: "bruto",
          text: "Bruto (Gr)",
        },
        {
          dataField: "kadar_jual",
          text: "Harga",
        },
        {
          dataField: "netto",
          text: "Netto (Gr)",
        },
        {
          dataField: "ongkos_rp",
          text: "Ongkos (Rp)",
          formatter: (cell) => {
            return <span>Rp. {parseFloat(cell).toLocaleString("ID-id")}</span>;
          },
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  type="button"
                  data-for="hapus"
                  className="btn btn-danger"
                  onClick={() =>
                    deleteIndex(
                      "pindahBarang",
                      rowIndex,
                      this.props.dispatch,
                      getListPindahBarang()
                    )
                  }
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash"></i>
                </button>
                <br />
                {/* <button
                  data-tip
                  data-for="edit"
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => this.setEdit(row)}
                >
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Edit</span>
                  </ReactTooltip>
                  <i className="fa fa-edit mr-1"></i>
                </button> */}
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getLokasiPindah());
    this.props.change("tanggal", getToday);
  }
  tambahModal() {
    this.props.dispatch(showModal());
    this.setState({ isEdit: false });
  }
  render() {
    return (
      <>
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-md-3 form-group">
              <Field
                component={renderField}
                type="date"
                name="tanggal"
                label="Tanggal"
              />
            </div>
            <div className="col-md-4 form-group">
              {this.props.dataLokasiPindah ? (
                <Field
                  component={renderFieldSelect}
                  label="Dari"
                  name="dari"
                  value={this.props.status}
                  normalize={(value) => value && value.toUpperCase()}
                  options={this.props.dataLokasiPindah.map((obj) => {
                    var payload = {
                      value: obj.kode_lokasi,
                      name: obj.nama_lokasi,
                    };
                    return payload;
                  })}
                ></Field>
              ) : (
                <SkeletonLoading label="Dari" />
              )}
            </div>
            <div className="col-md-1 text-center">
              <label htmlFor="" className="text-white">
                .
              </label>
              <h3>
                <i className="fa fa-exchange-alt"></i>
              </h3>
            </div>
            <div className="col-md-4 form-group">
              {this.props.dataLokasiPindah ? (
                <Field
                  component={renderFieldSelect}
                  label="Ke"
                  name="ke"
                  value={this.props.status}
                  normalize={(value) => value && value.toUpperCase()}
                  options={this.props.dataLokasiPindah.map((obj) => {
                    var payload = {
                      value: obj.kode_lokasi,
                      name: obj.nama_lokasi,
                    };
                    return payload;
                  })}
                ></Field>
              ) : (
                <SkeletonLoading label="Ke" />
              )}
            </div>
            <div className="col-md-4 form-group">
              {this.props.dataLokasiPindah ? (
                <Field
                  component={renderFieldSelect}
                  label="Kategori"
                  name="Kategori"
                  value={this.props.status}
                  normalize={(value) => value && value.toUpperCase()}
                  options={this.state.listKategori.map((obj) => {
                    var payload = {
                      value: obj.nama,
                      name: obj.nama,
                    };
                    return payload;
                  })}
                ></Field>
              ) : (
                <SkeletonLoading label="Kategori" />
              )}
            </div>
            <div className="col-md-4 form-group">
              <Field
                component={renderField}
                type="text"
                name="noBon"
                label="Bon Pindah"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-lg-4"></div>
            <div className="col-lg-4 col-md-4 mb-3">
              <div className="text-left"></div>
            </div>
            <div className="col-lg-4 col-md-4 mb-3">
              <div className="text-center">
                <button
                  className="btn btn-primary"
                  data-toggle="modal"
                  type="button"
                  data-target="#barcodeModal"
                >
                  BARCODE (+)
                </button>
                <button
                  className="btn btn-primary ml-2"
                  data-toggle="modal"
                  disabled
                >
                  BARCODE (-)
                </button>
              </div>
            </div>
            <div className="col-lg-4 col-md-4 mb-3">
              <div className="text-right">
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={() => this.tambahModal()}
                >
                  TAMBAH BARANG
                  <i className="fa fa-plus ml-3"></i>
                </button>
              </div>
            </div>
            <div className="col-md-12 col-lg-12">
              <GlobalTabel
                keyField="KodeBarang"
                data={this.props.listBarang || []}
                columns={this.state.columns}
                bordered={true}
                headerClasses="header-table"
              />
            </div>
          </div>
          <div className="col-lg-12">
            <div className="text-right">
              <button
                className="btn btn-primary"
                data-toggle="modal"
                disabled={this.props.onSend}
                onClick={this.props.handleSubmit}
              >
                {this.props.onSend ? (
                  <span
                    className="spinner-border spinner-border-sm mr-3"
                    aria-hidden="true"
                  ></span>
                ) : null}
                SIMPAN
                <i className="fa fa-paper-plane ml-3"></i>
              </button>
            </div>
          </div>
        </form>
        <div
          id="barcodeModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-xl">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Data Barcode
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormBarcode onSubmit={(data) => this.handleSubmit(data)} />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}
HeadPindahBarang = reduxForm({
  form: "headPindahBarang",
  enableReinitialize: true,
  validate: validasiHeadPindahBarang,
})(HeadPindahBarang);
export default connect((state) => {
  return {
    dataLokasiPindah: state.provinsi.dataLokasiPindah,
    listBarang: state.provinsi.pindahBarang,
  };
})(HeadPindahBarang);
