import React, { Component } from "react";
import { connect } from "react-redux";
import { reset } from "redux-form";
import Swal from "sweetalert2";
import { getClosePengambilanBarcode } from "../../actions/closeBarcodeAction";
import {
  getBruto,
  getNetto,
  getPkg,
  getQty,
  getRp,
} from "../../actions/LogicAction";
import { getListPindahBarang } from "../../actions/pindahBarangAction";
import { gagal } from "../../Alert";
import { AxiosMasterGet } from "../../Axios";
import { getToday } from "../../getDate";
import GlobalTabel from "../../GlobalComponent/GlobalTabel";

class FormBarcode extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "baki",
          text: "Nama Baki",
        },
        {
          dataField: "instansi",
          text: "Supplier",
        },
        {
          dataField: "jenis_trx",
          text: "Kode Barang",
        },
        {
          dataField: "total_bruto",
          text: "Berat(Gr)",
        },
        {
          dataField: "total_netto",
          text: "Netto(Gr)",
        },
        {
          dataField: "total_ongkos",
          text: "Ongkos(Rp)",
        },
        {
          dataField: "total_gross",
          text: "Gross(Gr)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() => this.pilihBarcode(row)}
                >
                  Pilih
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getClosePengambilanBarcode());
  }

  async pilihBarcode(data) {
    let hasil = data.detail_barang;
    hasil.forEach((list, index) => {
      AxiosMasterGet("jenis/get/by-kode-jenis/" + list.kode_jenis)
        .then((res) => {
          hasil[index].kode_kategori = res.data.kode_kelompok;
          hasil[index].kadar_modal = res.data.kadar_beli;
          hasil[index].kadar_jual = res.data.kadar_jual;
          hasil[index].p = 0;
          hasil[index].k = 0;
          hasil[index].qty = 1;
          delete hasil[index].kadar;
        })
        .then(() => {
          localStorage.setItem("pindahBarang", JSON.stringify(hasil));
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Transaksi Berhasil",
            showConfirmButton: false,
            timer: 1500,
          })
            .then(() => this.props.dispatch(reset("FormTambahBarang")))
            .then(() => this.props.dispatch(getListPindahBarang()))
            .then(() => this.getSumData());
        })
        .catch((err) => gagal(`Error Get Data , Detail : ${err}`));
    });
  }
  getSumData() {
    this.props.dispatch(getNetto("pindahBarang"));
    this.props.dispatch(getBruto("pindahBarang"));
    this.props.dispatch(getRp("pindahBarang"));
    this.props.dispatch(getPkg("pindahBarang"));
    this.props.dispatch(getQty("pindahBarang"));
  }

  render() {
    return (
      <div className="col-lg-12 col-md-12">
        <GlobalTabel
          keyField="kodeJenis"
          data={
            this.props.listCloseBarcode.filter(
              (list) =>
                list.jenis_trx === "PINDAH BARANG" &&
                list.tanggal === getToday()
            ) || []
          }
          columns={this.state.columns}
          textEmpty="Silahkan Tambah Data Dengan Klik Tombol Biru Diatas"
        />
      </div>
    );
  }
}

export default connect((state) => {
  return {
    listCloseBarcode: state.provinsi.listCloseBarcode,
  };
})(FormBarcode);
